/**
 * 
 */
import {IPosition} from 'pages/Game/types'

export interface IContext {
  state: IGame
  dispatch: (action: IAction) => void
  setting: ISetting,
  setSetting: (setting: ISetting) => void
  handleAction: () => void
}

/**
 * 
 */
export interface ISetting {
  // 
  rows: number
  // 
  cols: number
  // 
  width: number
  // 
  height: number
  // 
  speed: number
}

/**
 * 
 */
export const DefaultSetting: ISetting = {
  rows: 20,
  cols: 10,
  width: 25,
  height: 25,
  speed: 1
}

/**
 * 
 */
export interface ICell {
  // 
  position: IPosition
  // 
  width: number
  // 
  height: number
  // 
  fill?: string
}

/**
 * 
 */
export interface IGame {
  // 
  map: Map<string, string>
  minY: number
  maxY: number
  minX: number
  maxX: number
  // 
  shape?: IShape
  // 
  next: IShape
  // 
  locked: boolean
  // 
  scoreRows: number[]
  // 
  score: number
  // 
  level: number
  // 
  rows: number
  // 
  status?: Status
}


/**
 * 
 */
export interface IShape {
  // 
  type: string
  // 
  points?: IPosition[]
}

/**
 * 
 */
export interface IAction {
  // 
  type: ActionType
  // ()
  payload?: IShape | Direction | any
}

/**
 * 
 */
export enum ActionType {
  // 
  RUN,
  // 
  DRAW,
  // 
  CHANGE,
  // 
  MOVE,
  // 
  ONE_KEY,
  // 
  SCORE,
  // 
  STATUS,
  // 
  RESET
}

/**
 * 
 */
export enum Direction {
  // 
  DOWN,
  // 
  LEFT,
  // 
  RIGHT
}

/**
 * 
 */
export enum Status {
  // 
  PREPARE,
  // 
  START,
  // 
  STOP,
  // 
  END
}
