/**
 * 
 */
import {IPosition} from 'pages/Game/types'

export interface IContext {
  state: IGame
  dispatch: (action: IAction) => void
  setting: ISetting,
  setSetting: (setting: ISetting) => void
  handleAction: () => void
}

/**
 * 
 */
export interface ISetting {
  // 
  rows: number
  // 
  cols: number
  // 
  width: number
  // 
  height: number
  // 
  speed: number
}

/**
 * 
 */
export const DefaultSetting: ISetting = {
  rows: 20,
  cols: 20,
  width: 30,
  height: 30,
  speed: 1,
}

/**
 * 
 */
export interface ICell {
  // 
  position: IPosition
  // 
  width?: number
  // 
  height?: number
  // 
  type?: CellType
}

/**
 * 
 */
export interface IGame {
  // 
  map: Map<string, CellType>
  // 
  header: IPosition
  // 
  body: IPosition[]
  // 
  food: IPosition
  // 
  direction: Direction
  // 
  status: Status
}

/**
 * 
 */
export interface IAction {
  // 
  type: ActionType
  // ()
  payload?: Direction | Status | ISetting
}

/**
 * 
 */
export enum ActionType {
  // 
  RUN,
  /// 
  TURN,
  // 
  RESET,
  // 
  CHANGE_STATUS
}

/**
 * 
 */
export enum CellType {
  // 
  DEFAULT = 'default',
  // 
  FOOD = 'food',
  // 
  HEADER = 'header',
  // 
  BODY = 'body'
}

/**
 * 
 */
export enum Direction {
  // 
  UP,
  // 
  DOWN,
  // 
  LEFT,
  // 
  RIGHT
}

/**
 * 
 */
export enum Status {
  // 
  PREPARE,
  // 
  START,
  // 
  STOP,
  // 
  END
}

/**
 * 
 */
